 * @Last Modified by: xjn
 * @Last Modified time: 2022-09-06 17:33:57
    被保人信息录入
 */
<template>
  <div class="top">
    <div class="insured-people">
      <h4>
        <van-icon style="color: red" name="award" />
        被保人信息
      </h4>
      <div class="form">
        <van-form
          @submit="onSubmit"
          onkeydown="if(event.keyCode==13)return false;"
        >
          <van-field
            v-model="form.userName"
            name="姓名"
            label=""
            placeholder="请填写姓名"
            :rules="[{ required: true, message: '请填写姓名' }]"
            left-icon="friends-o"
          />
          <van-field
            v-model="form.idCard"
            name="身份证"
            label=""
            placeholder="请填写身份证"
            :rules="[
              { required: true, message: '请填写身份证' },
              { validator: idCardValidator, message: '身份证格式不正确' },
            ]"
            left-icon="idcard"
          >
            <!-- 身份证的扫描 -->
            <template #button>
              <van-icon name="scan" @click="scanIdCard" /> </template
          ></van-field>

          <van-field
            readonly
            clickable
            name="datetimePicker"
            :value="form.dateTime"
            label=""
            placeholder="请选择身份证有效期"
            @click="DateShowPicker = true"
            :rules="[{ required: true, message: '请选择身份证有效期' }]"
            left-icon="tosend"
            right-icon="arrow"
          >
            <!-- 点击长期 -->
            <template #button>
              <div class="longTime" @click.stop="longTime">长期</div>
            </template>
          </van-field>
          <!-- 时间选择器弹框 -->
          <van-popup v-model="DateShowPicker" position="bottom">
            <van-datetime-picker
              v-model="currentDate"
              type="date"
              title="选择年月日"
              :min-date="minDate"
              :max-date="maxDate"
              @confirm="DateOnConfirm"
              @cancel="DateShowPicker = false"
            />
          </van-popup>
          <!-- 手机号 -->
          <van-field
            v-model="form.phone"
            name="手机号"
            label=""
            type="number"
            placeholder="请填写手机号"
            :rules="[
              { required: true, message: '请填写手机号' },
              { validator: phoneValidator, message: '手机号格式不正确' },
            ]"
            left-icon="phone-o"
          />
          <van-field
            v-model="form.SMSCode"
            name="短信验证码"
            label=""
            placeholder="请填写短信验证码"
            :rules="[{ required: true, message: '请填写短信验证码' }]"
            left-icon="shield-o"
          >
            <template #button>
              <!-- <button @click.prevent="getSMSCode" type="button">asfas</button> -->
              <van-button
                @click.prevent="getSMSCode"
                type="primary"
                size="small"
                :disabled="SMSCodeDisable"
                >{{ SMSCodeButText }}</van-button
              >
            </template>
          </van-field>

          <!-- 短信滑块验证的弹窗 -->
          <van-popup :close-on-click-overlay="false" v-model="slideVerifyShow">
            <div @touchmove.prevent>
              <slide-verify
                :l="42"
                :r="10"
                :imgs="slideImgs"
                :w="310"
                :h="155"
                @success="onSuccess"
                @fail="onFail"
                @refresh="onRefresh"
                :slider-text="slideVerifyText"
              ></slide-verify>
            </div>
          </van-popup>

          <!-- 邮箱 -->
          <van-field
            v-model="form.email"
            name="邮箱"
            label=""
            placeholder="请填写常用邮箱"
            :rules="[
              { required: true, message: '请填写常用邮箱' },
              { validator: emailValidator, message: '邮箱格式不正确' },
            ]"
            left-icon="envelop-o"
          >
          </van-field>

          <!--       被保人信息 -->
          <div class="be-people">
            <h4>
              <van-icon style="color: red" name="award" />
              被保人信息
            </h4>
            <van-field
              v-model="form.userName"
              name="姓名"
              label=""
              placeholder="请填写姓名"
              :rules="[{ required: true, message: '请填写姓名' }]"
              left-icon="friends-o"
            />
            <van-field
              v-model="form.idCard"
              name="身份证"
              label=""
              placeholder="请填写身份证"
              :rules="[
                { required: true, message: '请填写身份证' },
                { validator: idCardValidator, message: '身份证格式不正确' },
              ]"
              left-icon="idcard"
            >
              <!-- 身份证的扫描 -->
              <template #button>
                <van-icon name="scan" @click="scanIdCard" /> </template
            ></van-field>

            <van-field
              readonly
              clickable
              name="datetimePicker"
              :value="form.dateTime"
              label=""
              placeholder="请选择身份证有效期"
              @click="DateShowPicker = true"
              :rules="[{ required: true, message: '请选择身份证有效期' }]"
              left-icon="tosend"
              right-icon="arrow"
            >
              <!-- 点击长期 -->
              <template #button>
                <div class="longTime" @click.stop="longTime">长期</div>
              </template>
            </van-field>
            <!-- 时间选择器弹框 -->
            <van-popup v-model="DateShowPicker" position="bottom">
              <van-datetime-picker
                v-model="currentDate"
                type="date"
                title="选择年月日"
                :min-date="minDate"
                :max-date="maxDate"
                @confirm="DateOnConfirm"
                @cancel="DateShowPicker = false"
              />
            </van-popup>
          </div>

          <!-- 学校 -->
          <van-field
            v-model="form.school"
            name="学校"
            label=""
            placeholder="请填写学校"
            :rules="[{ required: true, message: '请填写学校' }]"
            left-icon="wap-home-o"
          />

          <!-- 选择年级 -->
          <van-field
            readonly
            clickable
            name="picker"
            :value="form.gradeChoose"
            label=""
            placeholder="请选择年级"
            @click="gradeChooseShowPicker = true"
            :rules="[{ required: true, message: '请选择年级' }]"
            left-icon="user-o"
          >
            <template #button>
              <div>请选择<van-icon name="arrow" /></div>
            </template>
          </van-field>

          <!-- 选择年级弹框 -->
          <van-popup v-model="gradeChooseShowPicker" position="bottom">
            <van-picker
              show-toolbar
              :columns="gradeColumns"
              @confirm="gradeChooseOnConfirm"
              @cancel="gradeChooseShowPicker = false"
            />
          </van-popup>

          <!-- 选择班级 -->
          <van-field
            readonly
            clickable
            name="picker"
            :value="form.classChoose"
            :rules="[{ required: true, message: '请选择班级' }]"
            label=""
            placeholder="请选择班级"
            @click="classChooseShowPicker = true"
            left-icon="cluster-o"
          >
            <template #button>
              <div>请选择<van-icon name="arrow" /></div>
            </template>
          </van-field>

          <!-- 选择班级弹框 -->
          <van-popup v-model="classChooseShowPicker" position="bottom">
            <van-picker
              show-toolbar
              :columns="classColumns"
              @confirm="classChooseOnConfirm"
              @cancel="classChooseShowPicker = false"
            />
          </van-popup>

          <!-- 表单项类型 - 省市区选择器 -->
          <van-field
            readonly
            clickable
            name="area"
            :value="form.area"
            label=""
            placeholder="点击选择省市区"
            left-icon="location-o"
            @click="showArea = true"
            :rules="[{ required: true, message: '请选择省市区' }]"
          />
          <van-popup v-model="showArea" position="bottom">
            <van-area
              :area-list="areaList"
              @confirm="AreaonConfirm"
              @cancel="showArea = false"
            />
          </van-popup>

          <!--下一步  -->
          <div style="margin: 16px">
            <van-button
              round
              block
              type="info"
              native-type="submit"
              @click="gotoInsuranceApplication"
              >下一步</van-button
            >
          </div>
        </van-form>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from "vant";
import Vue from "vue";
import SlideVerify from "vue-monoplasty-slide-verify";

Vue.use(SlideVerify);
import isIdcard from "@/views/whiteList/JS/index";
import areaList from "./areaList";
export default {
  name: "",
  components: {},

  data() {
    return {
      slideImgs: [
        "https://img1.baidu.com/it/u=8957568,3772515509&fm=253&fmt=auto&app=138&f=JPEG?w=658&h=411",
        "https://img2.baidu.com/it/u=2703411705,3588113199&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=281",
        "https://img2.baidu.com/it/u=3116610008,4122310051&fm=253&fmt=auto&app=138&f=JPEG?w=889&h=500",
        "https://www4.bing.com//th?id=OHR.ArambolBeach_ZH-CN2149857876_1920x1080.jpg&rf=LaDigue_1920x1080.jpg&pid=hp",
        "https://img1.baidu.com/it/u=740578140,3088309952&fm=253&fmt=auto&app=120&f=JPEG?w=960&h=540",
        "https://img0.baidu.com/it/u=1995209268,3930309933&fm=253&fmt=auto&app=120&f=JPEG?w=667&h=500",
      ], // 可自定义拼图图片数组，未传则使用插件内默认的图片数组
      // 表单的内容
      form: {
        userName: "",
        idCard: "",
        dateTime: "",
        phone: "",
        SMSCode: "",
        school: "",
        gradeChoose: "",
        classChoose: "",
        email: "",
        area: "",
      },

      //   日期弹框
      DateShowPicker: false,
      DateShowPickerA: false,
      
      minDate: new Date(1900, 1, 1),
      maxDate: new Date(2100, 1, 1),
      currentDate: "",

      //   短信验证弹框
      slideVerifyShow: false,
      slideVerifyText: "滑动完成拼图",
      SMSCodeDisable: false,
      SMSCodeButText: "发送验证码",

      //   选择年级
      gradeColumns: [
        "一年级",
        "二年级",
        "三年级",
        "四年级",
        "五年级",
        "六年级",
      ],
      gradeChooseShowPicker: false,
      //   选择年级
      classColumns: ["一班", "二班", "三班", "四班", "五班", "六班"],
      classChooseShowPicker: false,
      // 地区
      showArea: false,
      areaList: areaList, // 数据格式见 Area 组件文档
    };
  },
  created() {
    //   获取当前日期
    let data = new Date();
    let year = data.getFullYear();
    let month = data.getMonth();
    let day = data.getDate();
    console.log(year, month, day);
    // 将现在日期赋值给日期选择器上
    this.currentDate = new Date(year, month, day);
  },
  mounted() {},
  methods: {
    //   点击提交 ，正确的时候
    onSubmit(values) {
      console.log("submit", values);
    },
    // 身份证格式的验证
    idCardValidator() {
      return isIdcard(this.form.idCard);
    },
    // 手机号格式的验证
    phoneValidator() {
      return /^1[3|4|5|6|7|8|9][0-9]\d{8}$/.test(this.form.phone);
    },
    //  email格式的验证
    emailValidator() {
      var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
      return reg.test(this.form.email);
    },
    // 选择身份证弹框中确认
    DateOnConfirm(time) {
      console.log(time.getFullYear());
      this.form.dateTime = `${time.getFullYear()}年${
        time.getMonth() + 1
      }月${time.getDate()}日`;
      this.DateShowPicker = false;
    },
    // 点击长期有效
    longTime() {
      this.form.dateTime = "长期有效";
    },

    SMSCodeCountdown() {
      let time = 10;
      let countdown = setInterval(() => {
        time--;
        this.SMSCodeButText = time + "秒后重试";
        if (time == 0) {
          this.SMSCodeButText = "发送验证码";
          this.SMSCodeDisable = false;
          clearInterval(countdown);
        }
      }, 1000);
    },
    // 获取短信验证码
    getSMSCode() {
      console.log("获取短信验证码");

      this.slideVerifyShow = true;
      this.onRefresh();
    },

    // 获取短信验证码成功
    onSuccess() {
      this.msg = "login success";
      this.slideVerifyShow = false;
      Toast.success("验证码获取成功");
      this.SMSCodeDisable = true;
      this.SMSCodeCountdown();
    },
    // 获取短信验证码失败
    onFail() {
      console.log("获取短信验证码失败");
    },
    // 点击刷新滑块
    onRefresh() {
      console.log("点击刷新滑块");
    },
    // 年级、选择
    gradeChooseOnConfirm(value) {
      this.form.gradeChoose = value;
      this.gradeChooseShowPicker = false;
    },
    // 班级、选择
    classChooseOnConfirm(value) {
      this.form.classChoose = value;
      this.classChooseShowPicker = false;
    },
    // 地区
    AreaonConfirm(values) {
      console.log("地区", values);
      this.form.area = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join("/");
      this.showArea = false;
    }, // 扫描身份证
    scanIdCard() {
      this.$router.push({
        path: "/scanIdCard",
      });
    },
    gotoInsuranceApplication() {
      this.$router.push({
        path: "/insuranceApplication",
      });
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='scss' scoped>
.van-icon {
  color: #0000ff;
}
.top {
  padding: 20px;
}
.form {
  margin-top: 20px;
}

:deep(.van-field__left-icon) {
  margin-right: 14px;
  color: #0066ff;
  width: 20px;
  height: 20px;
}
:deep(.van-field__left-icon .van-icon),
.van-field__right-icon .van-icon {
  font-size: 25px;
}

.longTime {
  color: blue;
  border-radius: 5px;
  border: #0000ff 1px solid;
  padding: 6px;
}

:deep(.van-cell) {
  padding: 17px 16px;
}

.be-people {
  margin-top: 10px;
}
</style>
